<template>
	<section class="page-wrap">
		<app-header/>
		<header class="section-masthead">
			<figure class="section-masthead__media">
				<lazy-image :src="imgSrc" :alt="imgAlt"/>
			</figure>
			<section class="section-masthead__text">
				<h1>Peroni Nastro Azzurro</h1>
				<p class="subtitle">Breaking into traditional beer occasions and making a forgotten brand, the supporter
					of Italian Millennials</p>
			</section>
		</header>
		<article class="work">
			<section class="work__detail">
				<h3 class="work__subtitle">Context</h3>
				<p>
					Personi Nastro Azzurro was born from a desire to become the best expression of premium Italian beer
					around the world. Named after the blue ribbon prize for the fastest boat in 1963, its logo contains
					its destiny: live every day as a journey, guided by instinct and by a sheer curiosity for the new.
				</p>
				<p>
					In 2015 Personi Nastro Azzurro was still one the most recognizable beer brands in the Italian
					market, but had lost its relevance among millennials who were increasingly preferring local,
					artisanal, craft-like beers or more appealing international brands.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Insight</h3>
				<p>
					Nastro Azzurro is an expression of Italian talent that wants to go further.
				</p>
				<p>
					Our target was probably the most talented, entrepreneurial and creative generations of Italians
					ever, but despite their potential they were struggling like never before to make their dreams come
					true.
				</p>
				<p>
					We also realized that the beer moment was becoming part of new rituals and conversations happening
					around projects, dreams and aspirations of our target.
				</p>
				<p>
					Based on these cultural insight we developed a new relevant role for the brand as supporter of the
					young, talented and entrepreneurial italians: the moving forward generation.
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Creative idea</h3>
				<p>
					A new creative platform «Ti porta lontano (Nastro Azzurro takes you further) was born. An invitation
					for all italian millennials to believe in their talent and actively follow their dreams.
				</p>
				<p>
					After a launch TVC manifesto, we have developed an ecosystem of tools, events, contests and
					activations to support our target in a very tangible way.
				</p>
				<p>
					After the first Manifesto campaign in 2015 we followed up in 2017 with a celebration of young
					Italians who «made it» by realizing their dreams and taking their talent further across the world.
					The first story being about Rocking1000, a group of friends who have created the «greatest band in
					the world».
				</p>
			</section>
			<section class="work__detail">
				<h3 class="work__subtitle">Impact</h3>
				<p>
					+ 10% increase in sales after the first 6 months of launch.
				</p>
				<p>
					In 2016 the brand registered the best performance of the last 7 years and regained its place as
					leader of the Italian Premium segment.
				</p>
				<p>
					450.000 participants to PNA supported events.
				</p>
				<p>
					250 articles mentioned the campaign.
				</p>
				<p>
					4.4 Millions contact reached with PNA digital platform.
				</p>
			</section>
		</article>
		<video-wrapper src="//www.youtube.com/embed/LADLAhA7hkM"
		               title="Bayer - Aspirin C"/>
		<app-footer/>
	</section>
</template>

<script>

	import LazyImage from './LazyImage';
	import AppHeader from './AppHeader';
	import AppFooter from './AppFooter';
	import VideoWrapper from './VideoWrapper';

	export default {
		name: 'Peroni',
		components: {
			LazyImage,
			AppHeader,
			AppFooter,
			VideoWrapper,
		},
		data() {
			return {
				imgSrc: '/static/philadelphia.jpg',
				imgAlt: 'Philadelphia'
			}
		}
	}
</script>

<style scoped>
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		margin-top: 2rem;
	}
	
	.section-masthead__text {
		padding: 1em 1.5em;
		background-color: var(--color__dark-alpha);
		transform: translateX(-10%);
	}
	
	.section-masthead__media {
		height: 100%;
		position: relative;
	}
	
	.section-masthead__media::after {
		content: '';
		position: absolute;
		top: -7px;
		left: 7px;
		width: 100%;
		height: 101%;
		background-color: var(--color__complementary);
		opacity: 0.5;
		mix-blend-mode: multiply;
		transform: rotate(0.5deg);
	}
	
	img {
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
	}
	
	h1 {
		font-size: 5vw;
	}
	
	p {
		margin-bottom: 0;
	}
	
	article {
		margin-top: 2em;
		margin-bottom: 2em;
	}
	
	.work__detail {
		border: 1px solid var(--color__dark-alpha);
		padding: 0.75em 1.25em;
	}
	
	.work__subtitle {
		background-color: var(--color__light);
		padding: 0 0.5em;
		transform: translateY(-1.5em);
		margin: 0;
		display: inline-block;
	}
	
	.work__subtitle + p {
		transform: translateY(-1.5em);
	}
	
	ul {
		margin: 0;
		list-style: none;
		padding-left: 0;
	}
	
	li {
		position: relative;
		padding-left: 1em;
	}
	
	li:not(:first-child) {
		margin-top: 0.75em;
	}
	
	li::before {
		content: '-';
		transform: scaleX(3) rotate(20deg);
		position: absolute;
		left: 0;
		top: 0;
	}
	
	@media screen and ( max-width: 64em ) {
		.section-masthead {
			margin-right: -3vw;
			margin-left: -3vw;
		}
		
		.work__detail:not(:first-child) {
			margin-top: 2em;
		}
	}
	
	@media screen and ( min-width: 64em ) {
		h1 {
			font-size: 3vw;
		}
		
		article {
			display: flex;
			background: linear-gradient(135deg, rgba(230, 230, 230, 0.2) 50%, var(--color__light-alpha-norm) 50%) no-repeat 0 -65%;
			background-size: 110% 110%;
			padding: 2em 0 0.5em 0.5em;
		}
		
		.work__detail {
			flex: 1;
		}
		
		.work__detail:not(:first-child) {
			margin-left: 1em;
		}
	}
</style>



